						fill="currentColor"
					/>
					<path
						d="M82.7979 53.3626C83.2583 53.6628 83.2583 54.3372 82.7979 54.6374L1.64807 107.551C1.01381 107.965 0.20792 107.3 0.497814 106.601C3.75747 98.7354 13.8885 72.7792 13.8886 54.0007C13.8887 35.2217 3.7575 9.26472 0.49781 1.39916C0.207924 0.699661 1.01381 0.0349653 1.64807 0.448537L82.7979 53.3626Z"
						stroke="white"
					/>
				</svg>
			</div>
		</div>
		<div class="main-banner-third">
			<h4 class="font-name">
				Казна
			</h4>
			<div class="line font-name">
				<div class="black-box" :style="backgroundBox" @click="decreaseFive">
					-5
				</div>
				<div class="black-box" :style="backgroundBox" @click="decrease">-</div>
				<div class="black-box-center" :style="backgroundBox">{{ counter }}</div>
				<div class="black-box" :style="backgroundBox" @click="add">+</div>
				<div class="black-box" :style="backgroundBox" @click="addFive">+5</div>
			</div>
		</div>
		<div class="main-banner-fourth">
			<div class="main-banner-btn font-name" :style="backgroundBox">
				<div class="main-banner-btn-text">Следующий ход</div>
				<div class="main-banner-btn-number">1</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	computed: {
		picture() {
			return require(`@/assets/${this.image}`)
		},
		background() {
			return `background-color: ${this.color}`
			// return 'background-color:' + this.color
		},
		backgroundBox() {
			return `background-color: ${this.boxColor}`
			// return 'background-color:' + this.color
		},
		colorSvg() {
			return `color: ${this.boxColor}`
			// return 'background-color:' + this.color
		},
	},
	components: {},
	data() {
		return {
			counter: 0,
			image: 'skull.png',
			color: '#26322B',
			boxColor: '#030F08',
			arrList: [
				{
					image: 'skull.png',
					color: '#26322B',
					boxColor: '#030F08',
				},
				{
					image: 'machine.png',
					color: '#442E22',
					boxColor: '#250D00',
				},
				{
					image: 'wave.png',
					color: '#632E2E',
					boxColor: '#410000',
				},
				{
					image: 'hieroglyph.png',
					color: '#4E2650',
					boxColor: '#2F0031',
				},
				{
					image: 'sun.png',
					color: '#2F747B',
					boxColor: '#096069',
				},
				{
					image: 'coffee.png',
					color: '#56585B',
					boxColor: '#201D23',
				},
				{
					image: 'tree.png',
					color: '#295455',
					boxColor: '#003334',
				},
				{
					image: 'lion.png',
					color: '#395696',
					boxColor: '#072C7C',
				},
				{
					image: 'indian.png',
					color: '#825602',
					boxColor: '#3B2700',
				},
				{
					image: 'jellyfish.png',
					color: '#005A90',
					boxColor: '#003555',
				},
			],
			index: 0,
		}
	},
	methods: {
		decreaseFive() {
			this.counter = this.counter - 5
		},
		decrease() {
			this.counter = this.counter - 1
		},
		add() {
			this.counter = this.counter + 1
		},
		addFive() {
			this.counter = this.counter + 5
		},
		leftClick() {
			console.log('Клик влево')
			if (this.index > 0) {
				this.index = this.index - 1
				this.color = this.arrList[this.index].color
				this.image = this.arrList[this.index].image
				this.boxColor = this.arrList[this.index].boxColor
			}
		},
		rightClick() {
			console.log('Клик вправо')
			if (this.index < this.arrList.length - 1) {
				this.index = this.index + 1
				this.color = this.arrList[this.index].color
				this.image = this.arrList[this.index].image
				this.boxColor = this.arrList[this.index].boxColor
			}
		},
	},
}
</script>

<style>
.main-banner {
	width: 100vw;
	height: auto;
	display: flex;
	flex-direction: column;
	background-color: #26322b;
	justify-content: flex-start;
}
