	display: flex;
	justify-content: center;
	margin: 0 0 20px 0;
}
.font-name {
	color: #ffffff;
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 600;
	align-items: center;
}
.img-icon {
	width: 50px;
	height: 50px;
	position: relative;
	top: -4px;
}
.main-banner-second {
	display: flex;
	justify-content: center;
	align-items: center;
}
.arrow-left {
	margin: 0 25px 0 0;
}
.skull {
	width: 110px;
}
.arrow-right {
	margin: 0 0 0 25px;
}
.img-arrow {
	width: 35px;
}
.main-banner-third {
	display: flex;
	flex-direction: column;
	padding: 0 20px;
}
.main-banner-third h4 {
	margin: 20px 0 10px 0;
}
.line {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.black-box {
	background-color: #030f08;
	color: #ffffff;
	width: 50px;
	height: 40px;
	margin-right: 35px;
	border-radius: 6px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.black-box:last-child {
	margin-right: 0;
}
.black-box-center {
	background-color: #030f08;
	color: #ffffff;
	width: 70px;
	height: 60px;
	margin-right: 35px;
	border-radius: 6px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.main-banner-fourth {
	display: flex;
	justify-content: center;
	margin: 5px 20px 10px 20px;
}
.main-banner-btn {
	background-color: #030f08;
	width: 100%;
	height: auto;
	text-align: center;
	border-radius: 8px;
	line-height: 300%;
	padding-left: 5%;
	display: flex;
}
.main-banner-btn-text {
	flex: 8;
	display: flex;
}
.main-banner-btn-number {
	flex: 2;
}
</style>
