<template>
	<div class="line bg-orange" :style="background">
		<img :src="picture" alt="Поле пшена" class="img-icon" />
		<h4 class="font-name">{{ text }}</h4>
		<img
			src="../assets/left-transparent.png"
			class="arrow-size"
			@click="decrease"
		/>
		<div>{{ counter }}</div>
		<img
			src="../assets/right-transparent.png"
			class="arrow-size"
			@click="add"
		/>
	</div>
</template>

<script>
export default {
	props: {
		image: {
			type: String,
			default: '',
		},
		text: {
			type: String,
			default: '',
		},
		color: {
			type: String,
			default: '',
		},
	},
	computed: {
		picture() {
			return require(`@/assets/${this.image}`)
		},
		background() {
			return `background-color: ${this.color}`
			// return 'background-color:' + this.color
		},
	},
	components: {},
	data() {
		return {
			counter: 0,
		}
	},
	methods: {
		decrease() {
			this.counter = this.counter - 1
		},
		add() {
			this.counter = this.counter + 1
		},
	},
}
</script>

<style>
.img-icon {
	width: 40px;
	height: 40px;
	position: relative;
	top: -4px;
}
.line {
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.bg-orange {
	background-color: #b57e37;
	height: 48px;
	align-items: center;
	display: flex;
	justify-content: space-around;
}
.arrow-size {
	width: 15px;
	height: 15px;
}
.font-name {
	color: #ffffff;
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 600;
	align-items: center;
}
</style>
